<h3>属性</h3>
<table class="table"><thead>
<tr>
<th>属性名</th>
<th>类型</th>
<th>描述</th>
<th>必须</th>
<th>默认</th>
</tr>
</thead>
<tbody>
<tr>
<td>num</td>
<td>Number</td>
<td>显示几个月数据</td>
<td>-</td>
<td>12</td>
</tr>
<tr>
<td>startSun</td>
<td>Boolean</td>
<td>日历是否以周日开始,默认是周一</td>
<td>-</td>
<td>false</td>
</tr>
<tr>
<td>date</td>
<td>String</td>
<td>默认激活日期字符串（'YYYY-MM-DD'）</td>
<td>-</td>
<td>-</td>
</tr>
<tr>
<td>data</td>
<td>Array</td>
<td>要在日期上显示的价格数组,单个对象格式为{date: 'YYYY-MM-DD', text: '124'}</td>
<td>-</td>
<td>[]</td>
</tr>
<tr>
<td>currency</td>
<td>String</td>
<td>货币符号</td>
<td>-</td>
<td>'￥'</td>
</tr>
<tr>
<td>btnText</td>
<td>String</td>
<td>确定按钮上的文字</td>
<td>-</td>
<td>'确定'</td>
</tr>
<tr>
<td>double</td>
<td>Boolean</td>
<td>开启往返2个日期的选择</td>
<td>-</td>
<td>false</td>
</tr>
<tr>
<td>date2</td>
<td>String</td>
<td>默认激活日期2字符串， double为true时有效</td>
<td>-</td>
<td>-</td>
</tr>
<tr>
<td>data</td>
<td>Array</td>
<td>要在日期上显示的价格数组,单个对象格式为{date: 'YYYY-MM-DD', text: '124'}， double为true时有效</td>
<td>-</td>
<td>[]</td>
</tr>
<tr>
<td>titles</td>
<td>Array</td>
<td>显示在日历顶部的文字，格式为['', '']， double为true时有效</td>
<td>-</td>
<td>-</td>
</tr>
<tr>
<td>week-bg</td>
<td>String</td>
<td>显示星期的容器的背景色样式</td>
<td>-</td>
<td>-</td>
</tr>
</tbody>
</table>
<h3>Slots</h3>
<table class="table"><thead>
<tr>
<th>Name</th>
<th>描述</th>
</tr>
</thead>
<tbody>
<tr>
<td>btn</td>
<td>自定义确定按钮</td>
</tr>
</tbody>
</table>
<h3>事件</h3>
<table class="table"><thead>
<tr>
<th>事件名</th>
<th>描述</th>
<th>返回值类型</th>
<th>说明</th>
</tr>
</thead>
<tbody>
<tr>
<td>selected</td>
<td>按确定按钮触发</td>
<td>Object</td>
<td>Array</td>
</tr>
</tbody>
</table>
</section></template>

<script>
  import addDays from 'date-fns/esm/addDays'
  import format from 'date-fns/esm/format'
  export default {
    data () {
      return {
        data: this.initData(),
        data2: this.initData()
      }
    },
    methods: {
      initData () {
        return Array.apply(null, {length: 365}).map((item, i) => ({
          date: format(addDays(new Date(), i), 'YYYY-MM-DD'),
          text: parseInt(Math.random() * 1000)
        }))
      }
    }
  }
</script>


