<template><section><h1>IndexSelect 索引选择器</h1>
<hr>
<blockquote>
<p>带索引和搜索功能的选择器组件，用于机场选择、城市选择、联系人选择等场景。索引条支持滑动选择</p>
</blockquote>
<h3>基础用法</h3>
<div class="demo-block demo-block-m">
  <air-index-select :data="data" :hots="hots" text-field="citynameCN" sub-text-field="airportnameCN" group-field="airportnameEN" search-hit="出发地中文/拼音/英文/机场三字码" :search-fields="searchFields" :tabs="tabs" key-field="itatCode" @on-selected="selected">
</air-index-select></div>

