        <span class="hljs-attr">hots</span>: [<span class="hljs-string">'WAW'</span>, <span class="hljs-string">'LAX'</span>, <span class="hljs-string">'GRU'</span>, <span class="hljs-string">'SCL'</span>, <span class="hljs-string">'TYO'</span>, <span class="hljs-string">'SHA'</span>, <span class="hljs-string">'CAN'</span>],
        <span class="hljs-attr">tabs</span>: [<span class="hljs-string">'国内'</span>, <span class="hljs-string">'国际'</span>]
      }
    },
    <span class="hljs-attr">methods</span>: {
      selected(item){
       <span class="hljs-built_in">console</span>.log(item)
      }
    }
