</div></demo-block>
<h3>使用了AirScroll组件的版本,滑动效果更好一些</h3>
<div class="demo-block demo-block-m">
  <air-index-list :data="data" :hots="hots" text-field="citynameCN" sub-text-field="airportnameCN" group-field="airportnameEN" search-hit="出发地中文/拼音/英文/机场三字码" :search-fields="searchFields" :tabs="tabs" key-field="itatCode" @on-selected="selected">
</air-index-list></div>
<demo-block>
                        <div slot="desc"><html><head></head><body><air-index-list :data="data" :hots="hots" text-field="citynameCN" sub-text-field="airportnameCN" group-field="airportnameEN" search-hit="出发地中文/拼音/英文/机场三字码" :search-fields="searchFields" :tabs="tabs" key-field="itatCode" @on-selected="selected">


