</span><span class="hljs-tag">&lt;/<span class="hljs-name">script</span>&gt;</span>
</code></pre>
</div></demo-block>
<h3>属性</h3>
<table class="table"><thead>
<tr>
<th>属性名</th>
<th>类型</th>
<th>描述</th>
<th>必须</th>
<th>默认</th>
</tr>
</thead>
<tbody>
<tr>
<td>key-field</td>
<td>String</td>
<td>主键字段</td>
<td><strong>是</strong></td>
<td>-</td>
</tr>
<tr>
<td>data</td>
<td>Array</td>
<td>要显示的数据</td>
<td><strong>是</strong></td>
<td>如果tabs不为空，那么data格式必须为[[],[]]</td>
</tr>
<tr>
<td>text-field</td>
<td>String</td>
<td>在列表中需要显示的字段名</td>
<td><strong>是</strong></td>
<td>-</td>
</tr>
<tr>
<td>group-field</td>
<td>String</td>
<td>分组字段</td>
<td><strong>是</strong></td>
<td>-</td>
</tr>
<tr>
<td>hots</td>
<td>Array</td>
<td>热点区域要显示的内容，通过key-field属性从data里面查询得到</td>
<td>-</td>
<td>[]</td>
</tr>
<tr>
<td>sub-text-field</td>
<td>String</td>
<td>在列表中显示的字段名-次要</td>
<td>-</td>
<td>''</td>
</tr>
<tr>
<td>search-hit</td>
<td>String</td>
<td>搜索框的placeholder</td>
<td>-</td>
<td>''</td>
</tr>
<tr>
<td>search-fields</td>
<td>Array</td>
<td>会在这个字段列表里面搜索</td>
<td>－</td>
<td>key-field的值</td>
</tr>
<tr>
<td>tabs</td>
<td>Array</td>
<td>用于在列表上显示一个可以切换的标签组,tabs的length和data的length必须一致</td>
<td>－</td>
<td>-</td>
</tr>
</tbody>
</table>
<h3>事件</h3>
<table class="table"><thead>
<tr>
<th>事件名</th>
<th>描述</th>
<th>返回值类型</th>
<th>说明</th>
</tr>
</thead>
<tbody>
<tr>
<td>on-selected</td>
<td>点击列表中的一项后触发</td>
<td>Object</td>
<td>数组data里面的一个对象</td>
</tr>
</tbody>
</table>
</section></template>

<script>
  import {airPort} from '../../packages/res'
  export default {
    data () {
      return {
        data: [airPort.filter(item => item.countrynameEN === 'CN'), airPort.filter(item => item.countrynameEN !== 'CN')],
        searchFields: ['itatCode','airportnameEN'],
        hots: ['WAW', 'LAX', 'GRU', 'SCL', 'TYO', 'SHA', 'CAN'],
        tabs: ['国内', '国际']
      }
    },
    methods: {
      selected(item){
       console.log(item)
      }
    }
  }
</script>


