<template><section><h1>AirCalenderH 横向日历</h1>
<hr>
<blockquote>
<p>以天为单位，横向滚动的日历,支持无限滚动和固定日期区间</p>
</blockquote>
<h3>基础用法</h3>
<div class="demo-block">
  <air-calender-h :data="data" v-model="date" :start-date="startDate" :end-date="endDate"></air-calender-h>
</div>
<demo-block>
                        <div slot="desc"><html><head></head><body><air-calender-h :data="data" v-model="date" :start-date="startDate" :end-date="endDate"></air-calender-h>


</body></html></div>
