  <span class="hljs-keyword">import</span> format <span class="hljs-keyword">from</span> <span class="hljs-string">'date-fns/esm/format'</span>
  <span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> {
    data () {
      <span class="hljs-keyword">return</span> {
        <span class="hljs-attr">startDate</span>: addDays(<span class="hljs-keyword">new</span> <span class="hljs-built_in">Date</span>(), <span class="hljs-number">-2</span>),
        <span class="hljs-attr">endDate</span>: addDays(<span class="hljs-keyword">new</span> <span class="hljs-built_in">Date</span>(), <span class="hljs-number">8</span>),
        <span class="hljs-attr">date</span>: <span class="hljs-keyword">new</span> <span class="hljs-built_in">Date</span>(),
        <span class="hljs-attr">data</span>: <span class="hljs-keyword">this</span>.initData()
      }
    },
    <span class="hljs-attr">methods</span>: {
      initData () {
        <span class="hljs-keyword">const</span> data = []
