<h3>属性</h3>
<table class="table"><thead>
<tr>
<th>属性名</th>
<th>类型</th>
<th>描述</th>
<th>必须</th>
<th>默认</th>
</tr>
</thead>
<tbody>
<tr>
<td>value</td>
<td>Date</td>
<td>用v-model绑定的值，日期变化会同步更新这个值</td>
<td>-</td>
<td>new Date()</td>
</tr>
<tr>
<td>start-date</td>
<td>Date</td>
<td>开始日期</td>
<td>-</td>
<td>new Date()</td>
</tr>
<tr>
<td>end-date</td>
<td>Date</td>
<td>结束日期, 不传就是无限滚动</td>
<td>-</td>
<td>new Date()</td>
</tr>
<tr>
<td>data</td>
<td>Array</td>
<td>在日期上显示的文本，如价格，格式为{date: 'YYYY-MM-DD', text: ''}</td>
<td>-</td>
<td>[]</td>
</tr>
<tr>
<td>next-text</td>
<td>String</td>
<td>下一天的文本，设置后，当前日期的下一天不显示实际日期，显示为设置的文本</td>
<td>-</td>
<td>-</td>
</tr>
<tr>
<td>pre-text</td>
<td>String</td>
<td>上一天的文本，设置后，当前日期的上一天不显示实际日期，显示为设置的文本</td>
<td>-</td>
<td>-</td>
</tr>
<tr>
<td>clickEnable</td>
<td>Boolean</td>
<td>除了点击左右按钮，点击日期也能切换</td>
<td>-</td>
<td>false</td>
</tr>
</tbody>
</table>
<h3>Slots</h3>
<table class="table"><thead>
<tr>
<th>Name</th>
<th>描述</th>
</tr>
</thead>
<tbody>
<tr>
<td>icon</td>
<td>激活的日期右边显示的图标</td>
</tr>
<tr>
<td>left</td>
<td>左边按钮</td>
</tr>
<tr>
<td>right</td>
<td>右边按钮</td>
</tr>
</tbody>
</table>
<h3>事件</h3>
<table class="table"><thead>
<tr>
<th>事件名</th>
<th>描述</th>
<th>返回值类型</th>
<th>说明</th>
</tr>
</thead>
<tbody>
<tr>
<td>click</td>
<td>点击激活的日期触发</td>
<td>Date</td>
<td>返回当前激活的日期</td>
</tr>
<tr>
<td>change</td>
<td>日期改变时触发</td>
<td>Date</td>
<td>返回当前激活的日期</td>
</tr>
<tr>
<td>input</td>
<td>日期改变时触发，这个事件在指定了v-model时生效，不要单独使用</td>
<td>Date</td>
<td>返回当前激活的日期</td>
</tr>
</tbody>
</table>
</section></template>
<style>
  .ccc{
     background-color: #4b3932;
     color: white;
  }
</style><style>
  .ccc{
     background-color: #4b3932;
     color: white;
  }
</style><style>
