  }
  .arrow{
   font-size: 20px;
  }
</style><style>
  .ccc{
     background-color: #4b3932;
     color: white;
  }
  .arrow{
   font-size: 20px;
  }
</style><style>
  .ccc{
     background-color: #4b3932;
     color: white;
  }
</style><style>
  .ccc{
     background-color: #4b3932;
     color: white;
  }
</style><style>
  .ccc{
     background-color: #4b3932;
     color: white;
  }
  .arrow{
   font-size: 20px;
  }
</style>
<script>
  import addDays from 'date-fns/esm/addDays'
  import format from 'date-fns/esm/format'
  export default {
    data () {
      return {
        date: new Date(),
        startDate: addDays(new Date(), -2),
        endDate: addDays(new Date(), 8),
        data: this.initData()
      }
    },
    methods: {
      click (date) {
        alert('点击了激活的日期')
      },
      change (date) {
        alert('日期改变了：' + date)
      },
      initData () {
        const data = []
        for (let i = -1; i < 365; i++) {
          data.push({
            date: format(addDays(new Date(), i), 'YYYY-MM-DD'),
            text: parseInt(Math.random() * 1000)
          })
        }
        return data
      }
    }
  }
</script>


